<template>
 <div class="load">
	<div class="load_text">Please enter your character's save id: <input v-model="id" placeholder="Save-Id"></div>
	<button @click="load">Load Save</button>
  </div>
</template>

<script>
//import axios from 'axios';
export default {
  name: "Load",
  data() {
	return {
      id: "",
    }
  },
  methods: {
    async load() {
		try {
//			await ...
			
			this.$root.$data.player.Race = 0;
			this.$root.$data.player.RaceName = "Human";
			this.$root.$data.player.Class = 0;
			this.$root.$data.player.ClassName = "Rogue";
			this.$root.$data.player.Level = 1;
			this.$root.$data.player.Exp = 0;
			this.$root.$data.player.Attack = 4;
			this.$root.$data.player.Defense = 1;
			this.$root.$data.player.Health = 10;
			this.$root.$data.player.MaxHealth = 10;
			this.$root.$data.player.Gold = 100;
			this.$root.$data.player.WeaponName = "N/A";
			this.$root.$data.player.WeaponWorth = 0;
			this.$root.$data.player.ArmorName = "N/A";
			this.$root.$data.player.ArmorWorth = 0;
			
			this.$root.$data.player.QuestTarget = 0;
			this.$root.$data.player.QuestCount = 0;
			this.$root.$data.player.QuestEnemy = "N/A";
			this.$root.$data.player.QuestWorth = 0;
			this.$root.$data.player.Location = "valentia";
			
			this.$alert("Click on 'Adventure' to continue!", "Load Successful!");
			
		} catch (error) {
			this.$alert("", "Invalid Save ID!");
		}
	}
  }
};
</script>

<style>

.load_text {
	padding-top: 15px;
	padding-bottom: 15px;
    font-family: 'Lucida Console';
}

</style>